import { TagModifier } from 'r34-types'
import { formatTagnameAndCount } from '../../misc/formatting'
import { ModifierStyledText } from '../common/ModifierStyledText'

interface TagNameProps {
  modifier: TagModifier
  name: string
  count?: number
}

export default function TagName(props: TagNameProps) {
  const { modifier, name, count } = props

  return (
    <ModifierStyledText modifier={modifier} tabIndex={0}>
      {formatTagnameAndCount(name, count)}
    </ModifierStyledText>
  )
}
